<template>
  <fragment>
    <tr v-for="(item, index) in paginatedData" :key="item.index">
      <td data-label="№">{{item.id}}</td>
      <td data-label="ИНН">
        <p v-if="!item.isEditing">{{item.INN}}</p>
        <input v-else class="input input--edit" type="text" v-model="item.INN">
      </td>
      <td data-label="Наименование">
        <p v-if="!item.isEditing">{{item.name}}</p>
        <input v-else class="input input--edit" type="text" v-model="item.name">
      </td>
      <td data-label="Руководитель">
        <p v-if="!item.isEditing">{{item.CEO}}</p>
        <input v-else class="input input--edit" type="text" v-model="item.CEO">
      </td>
      <td class="table__buttons">
        <button class="btn btn--table" @click="$emit('edit', item)">
          <span v-if="!item.isEditing">Редактировать</span>
          <span v-else>Применить</span>
        </button>
        <button class="btn btn--table" @click="$emit('remove', index)">Удалить</button>
      </td>
    </tr>
    <tr class="pagination__buttons">
      <td colspan="5" class="table__buttons">
        <button class="btn btn--table" :disabled="pageNumber === 0" @click="prevPage">Previous</button>
        <button class="btn btn--table" :disabled="pageNumber >= pageCount -1" @click="nextPage">Next</button>
      </td>
    </tr>
  </fragment>
</template>

<script>
export default {
  name: "Pagination",
  data() {
    return {
      pageNumber: 0 // по умолчанию 0
    };
  },
  props: {
    listData: {
      // объекты для отображение
      type: Array,
      required: true
    },
    size: {
      // количество данных на странице
      type: Number,
      required: false,
      default: 50
    }
  },
  methods: {
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    }
  },
  computed: {
    pageCount() {
      // узнаем сколько всего страниц
      let l = this.listData.length,
        s = this.size;
      return Math.ceil(l / s);
    },
    paginatedData() {
      // рассчитываем какие данные нужно показать
      const start = this.pageNumber * this.size,
        end = start + this.size;
      return this.listData.slice(start, end);
    }
  }
};
</script>

<style lang="sass">
.pagination__buttons
  margin: 0 auto
</style>
