<template>
  <div class="catalog" @click="closePrediction">
    <h1>Каталог</h1>
    <form autocomplete="off" class="form" @click="closePrediction">
      <div class="autocomplete">
        <input
          class="input"
          v-model="newINN"
          type="text"
          placeholder="ИНН"
          id="INN"
          @keyup="makePredict($event)"
        >
        <div v-if="isSuggested.INN">
          <AutocompleteField @selected="choosePrediction" :data="suggestions"/>
        </div>
      </div>

      <div class="autocomplete">
        <input
          class="input"
          v-model="newName"
          type="text"
          placeholder="Название"
          @keyup="makePredict($event)"
          id="name"
        >
        <div v-if="isSuggested.name">
          <AutocompleteField @selected="choosePrediction" :data="suggestions"/>
        </div>
      </div>
      <div class="autocomplete">
        <input
          class="input"
          v-model="newCEO"
          type="name"
          placeholder="Руководитель"
          @keyup="makePredict($event)"
          autocomplete="new-street-address"
          id="CEO"
        >
        <div v-if="isSuggested.CEO">
          <AutocompleteField @selected="choosePrediction" :data="suggestions"/>
        </div>
      </div>
      <button class="btn btn--form" @click.prevent="addItem">Добавить</button>
    </form>

    <table class="table" v-if="companies.length !=0">
      <thead>
        <tr>
          <th class="mobile-hidden">№</th>
          <th @click="sortByINN()">ИНН</th>
          <th @click="sortByName()">Наименование</th>
          <th @click="sortByCEO()">Руководитель</th>
          <th class="mobile-hidden">Управление</th>
        </tr>
      </thead>
      <tbody>
        <Pagination :list-data="companies" @edit="edit" @remove="remove"/>
      </tbody>
    </table>
  </div>
</template>

<script>
import Pagination from "./Pagination.vue";
import AutocompleteField from "./AutocompleteField.vue";

export default {
  name: "Catalog",
  components: {
    Pagination,
    AutocompleteField
  },
  data() {
    return {
      // если в хранилище пусто, то пустой массив
      companies: JSON.parse(localStorage.getItem("array")) || [],
      TOKEN: "6bf56fb9a1a7f4cfc5393b91b657befe083355ce",
      newINN: "",
      newName: "",
      newCEO: "",
      sort: {
        // технический объект для реализации сортировки. По-умолчанию по первому клику сортировка будет по возрастанию.
        INN: "asc",
        name: "asc",
        CEO: "asc"
      },
      suggestions: null, // список подсказок
      isSuggested: {
        // технический объект. Нужен чтобы определить в каком поле выводить подсказку
        INN: false,
        name: false,
        CEO: false
      }
    };
  },
  methods: {
    addItem() {
      if (this.newINN && this.newName && this.newCEO) {
        // проверка на заполнение всех полей
        this.companies.push({
          INN: this.newINN,
          name: this.newName,
          CEO: this.newCEO,
          isEditing: false,
          id: this.companies.length + 1
        });
        this.newINN = "";
        this.newName = "";
        this.newCEO = "";
        try {
          var serialObj = JSON.stringify(this.companies); //сериализуем его
          localStorage.setItem("array", serialObj);
        } catch (error) {
          alert("Закончилось место в хранилище. Запись не будет сохранена.");
        }
      } else {
        alert("Введите все данные");
      }
    },
    remove(index) {
      this.companies.splice(index, 1);
      // обновляем localStorage
      try {
        var serialObj = JSON.stringify(this.companies); //сериализуем его
        localStorage.setItem("array", serialObj);
      } catch (error) {
        alert("Закончилось место в хранилище. Запись не будет сохранена.");
      }
    },
    edit(item) {
      // если в записи isEditing === true, показываем инпуты, скрываем <td>
      item.isEditing = !item.isEditing;
      // обновляем localStorage
      try {
        var serialObj = JSON.stringify(this.companies); //сериализуем его
        localStorage.setItem("array", serialObj);
      } catch (error) {
        alert("Закончилось место в хранилище. Запись не будет сохранена.");
      }
    },
    sortByINN() {
      if (this.sort.INN === "asc") {
        this.companies = this.companies.sort((a, b) => {
          return a.INN - b.INN;
        });
        this.sort.INN = "desc"; // следующая сортировка будет наоборот
      } else {
        this.companies = this.companies.sort((a, b) => {
          return b.INN - a.INN;
        });
        this.sort.INN = "asc";
      }
    },
    sortByName() {
      if (this.sort.name === "asc") {
        this.companies = this.companies.sort((a, b) => {
          return a.name.localeCompare(b.name);
        });
        this.sort.name = "desc";
      } else {
        this.companies = this.companies.sort((a, b) => {
          return b.name.localeCompare(a.name);
        });
        this.sort.name = "asc";
      }
    },
    sortByCEO() {
      if (this.sort.CEO === "asc") {
        this.companies = this.companies.sort((a, b) => {
          return a.CEO.localeCompare(b.CEO);
        });
        this.sort.CEO = "desc";
      } else {
        this.companies = this.companies.sort((a, b) => {
          return b.CEO.localeCompare(a.CEO);
        });
        this.sort.CEO = "asc";
      }
    },
    makePredict(event) {
      const target = event.target;
      const text = event.target.value;
      this.$http
        // запрос на dadata
        .post(
          // прокси чтобы обойти CORS
          "https://cors-anywhere.herokuapp.com/https://suggestions.dadata.ru/suggestions/api/4_1/rs/suggest/party",
          { query: text },
          {
            headers: {
              "Content-Type": "application/json",
              Accept: "application/json",
              Authorization: `Token ${this.TOKEN}`
            }
          }
        )
        .then(
          response => {
            var dataArr = response.body.suggestions;
            this.suggestions = dataArr.slice(0, 5);
            this.autoComplete(target);
          },
          response => {
            console.log(response.status);
          }
        );
    },
    autoComplete(target) {
      // функция определяет в каком поле нужно показать подсказку
      if (target.id === "INN") {
        this.isSuggested.INN = true;
      } else if (target.id === "name") {
        this.isSuggested.name = true;
      } else {
        this.isSuggested.CEO = true;
      }
    },
    closePrediction() {
      // закрыть подсказки
      this.isSuggested.INN = false;
      this.isSuggested.name = false;
      this.isSuggested.CEO = false;
    },
    choosePrediction(item) {
      // функция копирует дату из выбранной подсказки
      this.newINN = item.data.inn;
      this.newName = item.value;
      if (item.data.management === null) {
        this.newCEO = "Неизвестно";
      } else {
        this.newCEO = item.data.management.name;
      }
      this.closePrediction();
    }
  }
};
</script>

